import * as React from "react";
import Layout from "../components/layout/Layout";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { Link, navigate, graphql } from "gatsby";
import { WrappedParse as Parse } from "../vendor/ParseApp";
import Card from "react-bootstrap/Card";
import { useTranslation } from "gatsby-plugin-react-i18next";
export default function App({ type = "signup" }) {
  const { t } = useTranslation();
  const [message, setMessage] = React.useState("");
  const [model, setModel] = React.useState({});

  const setModelValue = (name, value) => {
    setModel({
      ...model,
      [name]: value,
    });
  };

  return (
    <Container className="">
      <Row>
        <Col xs="0" md="3"></Col>
        <Col xs="12" md="6">
          <Card className="color-brand-1">
            <Card.Header>
              {" "}
              {type === "signup" ? t("signup") : t("login")}
            </Card.Header>

            <Card.Body>
              <Form>
                <Row>
                  <Col xs="12">
                    <Form.Group
                      className="mb-3"
                      controlId="exampleForm.ControlInput1"
                    >
                      <Form.Label>Email Address</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder=""
                        onChange={(v) => setModelValue("email", v.target.value)}
                      />
                    </Form.Group>
                  </Col>
                  <Col xs="12">
                    <Form.Group
                      className="mb-3"
                      controlId="exampleForm.ControlInput1"
                    >
                      <Form.Label>Password</Form.Label>
                      <Form.Control
                        type="password"
                        placeholder=""
                        onChange={(v) =>
                          setModelValue("password", v.target.value)
                        }
                      />
                    </Form.Group>
                  </Col>
                </Row>
                {type === "signup" && (
                  <>
                  <Button
                    variant="primary"
                    onClick={async () => {
                      try {
                        Parse.Cloud.run("createUser", model)
                          .then((e) => {
                
                            setMessage(t('verifyEmailClause'));
                          })
                          .catch((e: Exception) => {
                            setMessage(e?.message);
                          });
                      } catch (e: Exception) {
                        setMessage(e?.message);
                      }
                    }}
                  >
                    Sign Up
                  </Button>
                  <div>
                  {message}
                  </div>
                  </>
                )}{" "}
                {type === "login" && (
                  <>
                    <Button
                      variant="primary"
                      onClick={async () => {
                        try {
                          await Parse.User.logIn(model.email, model.password);
                          const profileItem = await Parse.Cloud.run(
                            "getMyProfile"
                          );
                          if (profileItem) {
                            navigate("/");
                          } else {
                            navigate("/account");
                          }
                        } catch (e: Exception) {
                          setMessage(e?.message);
                        }
                      }}
                    >
                      Login
                    </Button>{" "}
                    <Button
                      variant="primary"
                      onClick={async () => {
                        try {
                          await Parse.User.requestPasswordReset(model.email);
                        } catch (e: Exception) {
                          setMessage(e?.message);
                        }
                      }}
                    >
                      Reset Password
                    </Button>
                    <div>{message}</div>
                  </>
                )}
              </Form>
            </Card.Body>
          </Card>
        </Col>
        <Col xs="0" md="3"></Col>
      </Row>
    </Container>
  );
}

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
